<template>
  <div class="home">
    <div data-v-88fc0b64="" class="LoginFormss">
      <div data-v-88fc0b64="" class="login-container">
        <form @submit.prevent="login()" class="login-form">
          <h2 data-v-88fc0b64="">Dastur sahifasiga kirish</h2>
          <p data-v-88fc0b64="">Xodim tomonidan berilgan login va parol orqali dasturga kirib va undan foydalanishingiz
            mumkin</p><label data-v-88fc0b64="" for="login">Loginni kiriting</label>
          <input v-model="username" type="text" id="login" placeholder="Telefon raqam yoki maxsus nom">
          <label
              data-v-88fc0b64="" for="password">Parolni kiriting</label>
          <div data-v-88fc0b64="" class="password-container">
            <input v-model="password" type="password" id="password"  placeholder="Parolni kiriting" required="">
            <button data-v-88fc0b64="" type="button" class="toggle-password"></button>
          </div>
          <button data-v-88fc0b64="" type="submit">Dasturga kirish</button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      username:'',
      password:''
    }
  },
  methods:{
    login(){
      let formData = new FormData();
      formData.append("username", this.username);
      formData.append("password", this.password);

      this.$axios.post('/account/psychologists/login/', formData)
          .then(res=>{
            localStorage.setItem('token',res.data.access)
            this.$axios.get('/v1/students/me/', {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language': localStorage.getItem('lang')
              }
            })
                .then(res_main=>{
                  console.log(res_main.data.result.role)
                  if(res_main.data.result.role=='mainpsychologist'){
                    console.log(res_main.data.result)
                    this.$router.push('/')
                  }
                    if(res_main.data.result.role=='districtpsychologist'){
                    console.log(res_main.data.result)
                      this.$router.push(`districtpsychologist/school/${res_main.data.result.region_id}/?district_id=${res_main.data.result.district_id}`)

                  }
                  if(res_main.data.result.role=='regionpsychologist'){
                    console.log(res_main.data.result.region_id)
                    this.$router.push(`/regionpsychology/school/${res_main.data.result.region_id}`)
                  }

                })
            console.log(res)
          })

          .catch(()=>{

          })
    }
  }
}
</script>