<script>
import Navbar from "@/components/Navbar.vue";
import * as XLSX from "xlsx";

export default {
  components: {Navbar},
  data(){
    return{
      user_me:'',
      total_students:''
    }
  },
  mounted() {
    this.$axios.get('/v1/students/me/', {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': localStorage.getItem('lang')
      }
    })
        .then(res => {
          console.log(res.data)
          this.user_me=res.data
        })


    this.$axios.get(`/test/students-stats-by-schools/?region_id=${this.$route.params.region_id}&&class=${this.$route.query.class}&&district_id=${this.$route.query.district_id}`, {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': localStorage.getItem('lang')
      }
    })
        .then(res => {
          console.log(res.data)
          this.total_students=res.data
        })
  },
  methods:{
    exportTableToExcel() {
      // Jadval elementini olish
      const table = this.$refs.table;

      // Table ni worksheet ga aylantirish
      const workbook = XLSX.utils.table_to_book(table);

      // Excel faylni saqlash
      XLSX.writeFile(workbook, 'table-data.xlsx');
    }
  }
}
</script>